import React from 'react';
import PropTypes from 'prop-types';
import Particles from 'react-particles-js';
import './main.css';
import bridge from '@vkontakte/vk-bridge';


import {API} from './api';

import { Panel, PanelHeader, Header,CardGrid,Card,Alert,Subhead,InfoRow,ActionSheet,ActionSheetItem,Snackbar,MiniInfoCell, Separator, Tabs, TabsItem, Banner, Placeholder, List, Counter, SubnavigationBar, RichCell, SubnavigationButton, Title, PanelHeaderContent, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';
import { Icon24CubeBoxOutline,Icon28GiftCircleFillRed,Icon20VirusOutline,Icon20LikeOutline,Icon24CommentOutline,Icon20UserOutline,Icon28DoneOutline,Icon20SkullOutline,Icon28LikeOutline,Icon28SendOutline,Icon28CancelOutline,Icon28NarrativeOutline,Icon28Newsfeed,Icon24Linked,Icon28UserOutline,Icon24GiftOutline,Icon20ArrowUturnLeftOutline,Icon56Users3Outline,Icon24Reply,Icon28ArticleOutline, Icon24Users,Icon20ChevronUpOutline,Icon24HealthOutline,Icon36Play,Icon24SkullOutline } from '@vkontakte/icons';

const Home = ({ id, go, Info,SocketInfo, setChatBox, Referals, setPopout, fetchedUser,ActiveContent,setActiveContent,BuyVirus,BuyPerk,BuyItem }) => (
	<Panel id={id} style={{backgroundColor: "white"}}>
		{Info &&
		<Group>
		<div className="header">
		<center><h1>{Info.balance}</h1></center>
		<center><a style={{color: 'white'}}>Онлайн: {SocketInfo.online}</a></center>
		</div>
		<div className="underheader">
		</div>
		{Info.settings.anim === true && <Particles style={{position: 'absolute'}}
		params={{
		"particles": {
				"number": {
						"value": 25,
						"density": {
								"enable": true,
								"value_area": 800
						}
				},
				"line_linked": {
						"enable": false
				},
				"move": {
						"speed": 1,
						"out_mode": "out"
				},
				"shape": {
						"type": [
								"image"
						],
						"image": [
								{
										"src": "https://i.imgur.com/UHSDIej.png",
										"height": 20,
										"width": 23
								},
								{
										"src": "https://i.imgur.com/AsBOyEb.png",
										"height": 10,
										"width": 10
								},
								{
										"src": "https://i.imgur.com/I1zX3Wy.png",
										"height": 15,
										"width": 15
								}
						]
				},
				"size": {
						"value": 30,
						"random": false,
						"anim": {
								"enable": true,
								"speed": 2,
								"size_min": 5,
								"sync": true
						}
				}
		},
		"retina_detect": true
}}
		/>}
		 <div className="parent">
      <div className="div1">
			  <div className="podimg">
				 <img src={Info.info.photo_200}/>
			  </div>
		  </div>
      <div className="div2" style={{marginLeft: 16}}>
			  <Title className="parenttext" level="2" weight="regular">{Info.info.first_name} {Info.info.last_name}</Title>
				<MiniInfoCell className="parentinfo" before={<Icon20VirusOutline style={{marginRight: -8}}/>}>Вирус: {Info.virus.name}</MiniInfoCell>
				<MiniInfoCell className="parentinfo" before={<Icon20LikeOutline style={{marginRight: -8}}/>}>{Info.owner != null ? 'Заражен' : 'Не заражен'}</MiniInfoCell>
				<MiniInfoCell className="parentinfo" before={<Icon20UserOutline style={{marginRight: -8}}/>}>ID: {Info.id}</MiniInfoCell>
			</div>
     </div>
		</Group>}
		{Info &&
		<Group>
		<SubnavigationBar style={{marginTop: -24, marginBottom: -24}}>
			<SubnavigationButton before={<Icon24Linked fill="#ad8ef5"/>} onClick={async() => {
				setPopout(
					<ActionSheet onClose={() => setPopout(null)}>
					<Div>
					<InfoRow header="Заразить друга">
		        Тут ты можешь заразить друга ссылкой. Если зараженный перейдет по твоей ссылке и создаст свой вирус, он станет твоим зараженным.
		      </InfoRow>
					</Div>
					<List>
           <Cell expandable before={<Icon28SendOutline fill="#ad8ef5"/>} onClick={() => {bridge.send("VKWebAppShare", {"link": "https://vk.com/app7931491#r" + Info.id})}}>В сообщении</Cell>
           <Cell expandable before={<Icon28Newsfeed fill="#ad8ef5" />} onClick={() => { bridge.send("VKWebAppShowWallPostBox", {  "message": "Играть 👉🏻 https://vk.com/app7931491#r" + Info.id,  "attachments": "https://i.imgur.com/zaJfrFs.jpg"}) }}>На стене</Cell>
           <Cell expandable before={<Icon28NarrativeOutline fill="#ad8ef5"/>} onClick={async () => {
						 bridge.send('VKWebAppShowStoryBox', {
						 					 background_type: 'image',
						 					 url: "https://i.imgur.com/ZVuuixB.jpg",
						 					 locked: true,
						 					 attachment: {
						 					 text: 'go_to',
						 					 type: "url",
						 					 url: "https://vk.com/app7931491"
						 					 	}})
					 }}>В истории</Cell>
           <Cell expandable before={<Icon28CancelOutline fill="red"/>} onClick={() => {setPopout(null)}}>Закрыть</Cell>
          </List>
					</ActionSheet>
				)
			}}className="subbutton" textLevel={3} size="m">
						 Заразить друга
			</SubnavigationButton>
			<SubnavigationButton before={<Icon24GiftOutline fill="#ad8ef5"/>} className="subbutton" textLevel={3} size="m" onClick={async() => {
				setPopout(
					<ActionSheet onClose={() => setPopout(null)}>
					<Div>
					<InfoRow header="Заразить друга">
		        Тут ты можешь получить небольшие бонусы.
		      </InfoRow>
					</Div>
					<List>
           <Cell before={Info.settings.group === true ? <Icon28DoneOutline fill="#ad8ef5"/> : <Icon28GiftCircleFillRed fill="#ad8ef5"/>} onClick={async () => {
						 try{
							 const result = await bridge.send("VKWebAppJoinGroup", {"group_id": 1});
							 if(result.result === true){
								 const json = await API('reward', 'type=group')
								 json.status == 'good' ? Info.settings.group = true : ''
								 json.status == 'good' ? Info.balance = +Info.balance + 5 : ''
								 setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> {json.message} </Snackbar>)
							 }else{
								 setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> Ошибка! </Snackbar>)
							 }
						 }catch(e){
							 setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> Ошибка! </Snackbar>)
						 }
					 }}>Подпишись на нашу группу - 5 ДНК</Cell>
           <Cell before={Info.settings.favorites === true ? <Icon28DoneOutline fill="#ad8ef5"/> : <Icon28GiftCircleFillRed fill="#ad8ef5"/>} onClick={async () => {
						 try{
							 const result = await bridge.send("VKWebAppAddToFavorites");
							 if(result.result === true){
								 const json = await API('reward', 'type=favorites')
								 json.status == 'good' ? Info.settings.favorites = true : ''
								 json.status == 'good' ? Info.balance = +Info.balance + 3 : ''
								 setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> {json.message} </Snackbar>)
							 }else{
								 setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> Ошибка! </Snackbar>)
							 }
						 }catch(e){
							 setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> Ошибка! </Snackbar>)
						 }
					 }}>В избранное - 3 ДНК</Cell>
           <Cell  before={<Icon28CancelOutline fill="red"/>} onClick={() => {setPopout(null)}}>Закрыть</Cell>
          </List>
					</ActionSheet>
				)
			}}>
						 Бонус
			</SubnavigationButton>
			<SubnavigationButton before={<Icon24CommentOutline fill="#ad8ef5"/>} className="subbutton" textLevel={3} size="m" data-to="chat" onClick={go}>
						 Чат
			</SubnavigationButton>
		 </SubnavigationBar>
		</Group>}
		{Info &&
		<Group>
		{Info.owner != null &&
		<Banner
				 header="Вы болеете!"
				 subheader={`Вы болеете вирусом "${Info.owner.name}", излечитесь чтобы перестать отдавать ${(Info.owner.out/10).toFixed(3)} ДНК в час. Пока вы болеете, вы не можете ${Info.owner.perk === 'update' ? 'прокачивать свой вирус' : Info.owner.perk === 'zaraza' ? 'заражать других людей' : 'покупать предметы'}.`}
				 actions={<React.Fragment>
					 <div style={{display: 'flex'}}>
					 <Button size="s" after={<Icon24HealthOutline/>} className="secondbuttons" onClick={async () => {
						 const response = await API('out')
						 response.status ? setPopout(<Snackbar duration={1000} onClose={() => setPopout(null)}> {response.message} </Snackbar>) : ''
						 response.status == 'good' ? Info.owner = null : ''
					 }}>Лечиться за {Info.owner.out.toFixed(3)} ДНК</Button>
					 <Button size="s" style={{marginLeft: 8}} before={<Icon24Reply/>} className="secondbuttons" onClick={async () => {
						 bridge.send('VKWebAppShowStoryBox', {
						 					 background_type: 'image',
						 					 url: "https://i.imgur.com/zaJfrFs.jpg",
						 					 locked: true,
						 					 attachment: {
						 					 text: 'go_to',
						 					 type: "url",
						 					 url: "https://vk.com/app7931491"
						 					 	}})
					 }}></Button>
					 </div>
				         </React.Fragment>}
			 />}
			 <Tabs>
	      <TabsItem
	       onClick={() => setActiveContent('virus')}
	       selected={ActiveContent === 'virus'}>
				 Мой вирус
				 </TabsItem>
				 <TabsItem
 	       onClick={() => setActiveContent('items')}
 	       selected={ActiveContent === 'items'}>
 				 Предметы
 				 </TabsItem>
				<TabsItem
				 after={<Counter size="s">{Referals.length}</Counter>}
	       onClick={() => setActiveContent('refs')}
	       selected={ActiveContent === 'refs'}>
				 Зараженные
				 </TabsItem>
	     </Tabs>
		  {Referals.length > 0 && ActiveContent == 'refs' &&
              <React.Fragment>
							<Header mode="secondary" aside={<Button size="s" style={{color: '#ad8ef5'}}before={<Icon20ArrowUturnLeftOutline/>} mode="tertiary" onClick={async () => {
								const response = await API('getprible')
								response?.response?.balance ? Info.balance = response.response.balance.toFixed(3) : ''
								setPopout(<Alert
								 actions={[{
								 title: 'Закрыть',
								 autoclose: true,
								 mode: 'cancel'
							 }]}
							 actionsLayout="vertical"
							 onClose={() => setPopout(null)}
							 header={response.status === 'good' ? "Прибыль собрана!" : "Ошибка!"}
							 text={response.status === 'good' ? "Вас не было " + response.response.time + " час(а). Собрано " + response.response.prible.toFixed(3) + " ДНК!" : response.message}
						 />)
							}}>Собрать</Button>}>Зараженные / <a style={{color: '#ad8ef5'}}>{(Info.virus.out/10 * Referals.length).toFixed(3)} в час</a> </Header>
                {Referals.map((item) => {
                  return (
										<RichCell
								        before={<Avatar size={48} src={item.infovk.photo_200} />}
								        caption={(Info.virus.out/10).toFixed(3) + " в час"}
												key={item.id}
								      >
								       {item.infovk.first_name} {item.infovk.last_name}
								      </RichCell>
                  )
    })}
		</React.Fragment>}
		{Referals.length === 0 && ActiveContent == 'refs' &&
		<Placeholder
		 action={<Button size="s" after={<Icon24HealthOutline/>} className="secondbuttons" onClick={() => {API('virus')}}>Заразить друга</Button>}
		 icon={<img src="https://i.imgur.com/02raeoR.png" style={{width: 96, height: 96}} />}
		>
        У вас еще нет зараженных. Заразить можно через ссылку (вероятность 100%) или через поиск в топе (вероятность зависит от прокачки вируса).
    </Placeholder>
		}
		{ActiveContent == 'virus' &&
		<React.Fragment>
		<Header mode="secondary">Вирус {Info.virus.name}</Header>
		<CardGrid size="s">
          <Card mode="shadow">
            <div className="cards">
						  <img src='https://i.imgur.com/B4cqIvK.png' />
							<Subhead weight="medium" style={{marginTop: '8px'}}>Заразность: {Info.virus.zaraza}%</Subhead>
							<Button size="s" before={<Icon20ChevronUpOutline/>} style={{marginTop: '8px'}} className="secondbuttons" onClick={() => BuyVirus()}>Прокачать</Button>
            </div>
          </Card>
          <Card mode="shadow">
					<div className="cards">
						<img src='https://i.imgur.com/kVf3diA.png' />
						<Subhead weight="medium" style={{marginTop: '8px'}}>Лечение: {Info.virus.out.toFixed(3)} ДНК</Subhead>
						<Button size="s" before={<Icon20ChevronUpOutline/>} style={{marginTop: '8px'}} className="secondbuttons" onClick={() => BuyVirus()}>Прокачать</Button>
					</div>
          </Card>
          <Card mode="shadow">
					<div className="cards">
					<img src='https://i.imgur.com/bkBYjal.png' />
					<Subhead weight="medium" style={{marginTop: '8px'}}>Способность</Subhead>
					<Button size="s" before={<Icon20SkullOutline/>} style={{marginTop: '8px'}} className="secondbuttons" onClick={() => BuyPerk()}>Выбрать</Button>
					</div>
          </Card>
    </CardGrid>
		</React.Fragment>
		}
		{ActiveContent == 'items' &&
		<React.Fragment>
		<Header mode="secondary">Инвентарь</Header>
		<CardGrid size="s">
          <Card mode="shadow">
            <div className="cards">
						  <img src='https://i.imgur.com/IKSK8Un.png' />
							<Subhead weight="medium" style={{marginTop: '8px'}}>Маска: {Info.items.maska.count} шт.</Subhead>
							<Button size="s" before={<Icon20ChevronUpOutline/>} style={{marginTop: '8px'}} className="secondbuttons" onClick={() => BuyItem()}>Купить</Button>
            </div>
          </Card>
          <Card mode="shadow">
					<div className="cards">
						<img src='https://i.imgur.com/HFRg3T3.png' />
						<Subhead weight="medium" style={{marginTop: '8px'}}>Перчатки: {Info.items.perchatki.count} шт.</Subhead>
						<Button size="s" before={<Icon20ChevronUpOutline/>} style={{marginTop: '8px'}} className="secondbuttons" onClick={() => BuyItem()}>Купить</Button>
					</div>
          </Card>
          <Card mode="shadow">
					<div className="cards">
					<img src='https://i.imgur.com/ynNTMOP.png' />
					<Subhead weight="medium" style={{marginTop: '8px'}}>Антисептик: {Info.items.antiseptik.count} шт.</Subhead>
					<Button size="s" before={<Icon20ChevronUpOutline/>} style={{marginTop: '8px'}} className="secondbuttons" onClick={() => BuyItem()}>Купить</Button>
					</div>
          </Card>
    </CardGrid>
		</React.Fragment>
		}
		</Group>}
		<div style={{marginTop: '10%'}}></div>
	</Panel>
);

Home.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Home;
