import React from 'react';
import PropTypes from 'prop-types';
import Particles from 'react-particles-js';
import './main.css';
import bridge from '@vkontakte/vk-bridge';
import {API, GETTOP} from './api';

import { Panel, PanelHeader,Search,PanelHeaderBack,Header,Snackbar,ActionSheet,PanelSpinner,MiniInfoCell,Subhead, Tabs,Switch, TabsItem, Banner, Placeholder, List, Counter, SubnavigationBar, RichCell, SubnavigationButton, Title, PanelHeaderContent, Button, Group, Cell, Div, Avatar } from '@vkontakte/vkui';
import { Icon28VideoOutline,Icon28CopyOutline,Icon28FavoriteOutline,Icon28ChatsOutline,Icon28Users3Outline } from '@vkontakte/icons';

const Settings = ({ id, go, Info, setPopout}) => (
	<Panel id={id} style={{background: "white"}}>
	<PanelHeader>Настройки</PanelHeader>
	<Group header={<Header mode="secondary">Игра</Header>}>
	<List>
			<Cell expandable before={<Icon28VideoOutline fill="#ad8ef5"/>} after={<Switch defaultChecked={Info.settings.anim === true} onClick={async () => {
        const response = await API('settings', 'type=anim')
				Info.settings.anim = response.response
				setPopout(<Snackbar
				duration={1000}
        onClose={() => setPopout(null)}>
        Анимация {response.response === true ? "включена" : "выключена"}
        </Snackbar>)
			}}/>}>Вкл/Выкл анимацию</Cell>
			<Cell expandable before={<Icon28CopyOutline fill="#ad8ef5"/>} onClick={() => {
				bridge.send("VKWebAppCopyText", {"text": "https://vk.com/app7931491#r" + Info.id});
				setPopout(<Snackbar
				duration={1000}
        onClose={() => setPopout(null)}>
        Ссылка скопирована
        </Snackbar>)
			}}>Копировать реф. ссылку</Cell>
			<Cell expandable before={<Icon28FavoriteOutline fill="#ad8ef5"/>} onClick={() => {
				bridge.send("VKWebAppAddToFavorites");
			}}>Добавить в избранное</Cell>
	</List>
	</Group>
	<Group header={<Header mode="secondary">Контакты</Header>}>
	<List>
			<Cell expandable before={<Icon28ChatsOutline fill="#ad8ef5"/>}>Беседа игры</Cell>
			<Cell expandable before={<Icon28Users3Outline fill="#ad8ef5"/>}>Группа игры</Cell>
	</List>
	</Group>
	<div style={{marginTop: '100px'}}>
	</div>
	</Panel>
);

Settings.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
	fetchedUser: PropTypes.shape({
		photo_200: PropTypes.string,
		first_name: PropTypes.string,
		last_name: PropTypes.string,
		city: PropTypes.shape({
			title: PropTypes.string,
		}),
	}),
};

export default Settings;
